$domino-height: 60px;
$seven: 14px;

body {
  background-color: #282c34;
}

.App {
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes slideDown {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(60px);
  }
}

.slide-down {
  animation-duration: 1s;
  animation-name: slideDown;
}


@keyframes slideUp {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-60px);
  }
}

.slide-up {
  animation-duration: 1s;
  animation-name: slideUp;
}


.hand {
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: $domino-height;
}

.player-hand:hover {
  background-color: #0061FF;
  border-radius: 4px;
}

.domino-left-low {
  height: $domino-height;
  rotate: -90deg;
  margin: 1px 1px;
}

.domino-left-high {
  height: $domino-height;
  rotate: 90deg;
  margin: 1px 1px;

}

.domino-vertical {
  height: $domino-height;
  margin: 1px 1px;

}

.domino-upsidedown {
  height: $domino-height;
  rotate: 180deg;
  margin: 1px 1px;
}

.domino-hover {
  background-color: #0061FF;
  border-radius: 4px;
}

.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 500px;
}

.played-cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.row-2 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-self: flex-start;
}

.bottom-domino-row {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  // margin-left: -14px;
}

.row-4 {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
}

.middle-row-horizontal-margin {
  margin: 0 $seven;
}

.horizontal-on-vertical-branch {
  margin-top: -8px;
  margin-bottom: -8px;
}

.lower {
  margin-bottom: -14px;

}

.higher {
  margin-top: -14px;
}

.row-1 {
  display: flex;
  flex-direction: row-reverse;
  align-self: flex-start;
  margin-bottom: -$seven;
}

.row-5 {
  display: flex;
  flex-direction: row-reverse;
  margin-top: -$seven;
}

.pass-button {
  margin-top: 50px;
}

.slight-vertical-margin {
  margin: 30px 0px;
}

.info-icon {
  width: 20px;
}

.computerHand {
  @media (max-width: 750px) {
    margin-top: 60px;
  }
}

:root {
  --delay: .5s;
  --timing: .7s;

  --delay: .5s;
  --timing: .7s;
}

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -ms-box-sizing: border-box;
}

.box-outer {
  display: flex;
  overflow: hidden;
  width: 20rem;
  height: 20rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main_box {
  width: 900px;

  width: 20rem;
  height: 20rem;
  position: relative;
  background-color: white;
  border: 0.5rem solid #00e0ff;
}

.bar {
  position: absolute;
  width: 5rem;
  height: .5rem;
  background: #fff;
  transition: all var(--timing) linear;
  -webkit-animation-duration: var(--timing);
  animation-duration: var(--timing);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.bar.delay {
  animation-delay: var(--delay);
  -webkit-animation-delay: var(--delay);
}

.top {
  top: -.5rem;
  left: -0.5rem;
}

.right {
  top: 1.8rem;
  right: -2.7rem;
  transform: rotate(90deg);
}

.bottom {
  bottom: -0rem;
  left: -0.5rem;
}

.left {
  top: 1.8rem;
  left: -2.1rem;
  transform: rotate(90deg);

}

@-webkit-keyframes h-move {
  0% {
    left: -0.5rem;
  }

  100% {
    left: 20rem;
  }
}

@keyframes h-move {
  0% {
    left: -0.5rem;
  }

  100% {
    left: 20rem;
  }
}

.top,
.bottom {
  -webkit-animation-name: h-move;
  animation-name: h-move;
}

@-webkit-keyframes v-move {
  0% {
    top: -0.5rem;
  }

  100% {
    top: 22.8rem;
  }
}

@keyframes v-move {
  0% {
    top: -0.5rem;
  }

  100% {
    top: 22.8rem;
  }
}

.right,
.left {
  -webkit-animation-name: v-move;
  animation-name: v-move;
}

.modal-text-box {
  margin: 10px;
  text-align: center;
}